
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: 'taskitem',
data () {
return {
badgeText: '变更'
}
},
props: {
item: {
type: Object,
default: null
},
path: {
type: String,
default: ''
},
belong: {
type: String,
default: 'my'
}
},
components: {
Badge
},
methods: {
itemClick (val) {
window.postMessage('updateCurrentTaskid:' + this.item.mdLayoverTaskId, '*')
this.$router.push({ path: this.path, query: { item: this.item, belong: this.belong, udf1: this.item.udf1, taskid: this.item.mdLayoverTaskId, acid: this.item.acReg } })
}
}
}



